<template>
  <div class="movie-detail-container">
    <!-- Î°úÎî© Ï§ë -->
    <div v-if="loading" class="loading-container">
      <p>Î°úÎî© Ï§ë...</p>
    </div>

    <!-- ÏòÅÌôî ÏÉÅÏÑ∏ Ï†ïÎ≥¥ -->
    <div v-else-if="movie" class="movie-detail-card">
      <!-- ÏôºÏ™Ω ÏÑπÏÖò: Ìè¨Ïä§ÌÑ∞ Î∞è Ï†úÎ™© -->
      <div class="movie-detail-left">
        <img
          :src="movie.poster_path 
            ? `https://image.tmdb.org/t/p/w500${movie.poster_path}` 
            : 'https://via.placeholder.com/500x750?text=No+Image' "
          :alt="movie.title"
          class="movie-poster"
        />
        <h1 class="movie-title">{{ movie.title }}</h1>
      </div>

      <!-- Ïò§Î•∏Ï™Ω ÏÑπÏÖò: ÏÑ§Î™Ö Î∞è Ï∂îÍ∞Ä Ï†ïÎ≥¥ -->
      <div class="movie-detail-right">
        <p class="movie-overview">{{ movie.overview || "ÏòÅÌôî ÏÑ§Î™ÖÏù¥ ÏóÜÏäµÎãàÎã§." }}</p>
        <p class="movie-info">
          <strong>Í∞úÎ¥âÏùº:</strong> {{ movie.release_date || "Ï†ïÎ≥¥ ÏóÜÏùå" }}
        </p>
        <p class="movie-info">
          <strong>ÌèâÏ†ê:</strong> ‚≠ê{{ movie.vote_average || "N/A" }}/10
        </p>
        <!-- Ï∞úÌïòÍ∏∞ Î≤ÑÌäº -->
        <button @click="addToWishlist" class="wishlist-btn">Ï∞úÌïòÍ∏∞</button>
      </div>
    </div>

    <!-- Í¥ÄÎ†® Ï∂îÏ≤ú ÏòÅÌôî -->
    <div v-if="recommendations.length" class="recommendations-section">
      <h2>Í¥ÄÎ†® Ï∂îÏ≤ú ÏòÅÌôî</h2>
      <div class="recommendations-grid">
        <router-link
          v-for="recommendation in recommendations"
          :key="recommendation.id"
          :to="`/movie/${recommendation.id}`"
          class="recommendation-item"
        >
          <img
            :src="recommendation.poster_path 
              ? `https://image.tmdb.org/t/p/w500${recommendation.poster_path}` 
              : 'https://via.placeholder.com/500x750?text=No+Image' "
            :alt="recommendation.title"
          />
          <h3>{{ recommendation.title }}</h3>
        </router-link>
      </div>
    </div>

    <!-- ÏòÅÌôî Îç∞Ïù¥ÌÑ∞Î•º Ï∞æÏùÑ Ïàò ÏóÜÏùÑ Îïå -->
    <div v-else class="error-message">
      <p>ÏòÅÌôîÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§. ÎÇòÏ§ëÏóê Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî.</p>
    </div>
  </div>
</template>

<script>
import tmdb from "@/api/tmdb";

export default {
  name: "MovieDetailView",
  data() {
    return {
      movie: null, // ÏòÅÌôî ÏÉÅÏÑ∏ Ï†ïÎ≥¥
      recommendations: [], // Ï∂îÏ≤ú ÏòÅÌôî Î¶¨Ïä§Ìä∏
      loading: true, // Î°úÎî© ÏÉÅÌÉú
    };
  },
  methods: {
    // TMDB APIÎ°ú ÏòÅÌôî ÏÉÅÏÑ∏ Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
    async fetchMovieDetails() {
      try {
        const movieId = this.$route.params.id; // URL ÌååÎùºÎØ∏ÌÑ∞ÏóêÏÑú ÏòÅÌôî ID Í∞ÄÏ†∏Ïò§Í∏∞
        const response = await tmdb.get(`/movie/${movieId}`, {
          params: { language: "ko-KR" },
        });
        this.movie = response.data; // ÏòÅÌôî Îç∞Ïù¥ÌÑ∞ Ï†ÄÏû•
      } catch (error) {
        console.error("Failed to fetch movie details:", error);
        alert("ÏòÅÌôî Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî.");
      } finally {
        this.loading = false; // Î°úÎî© ÏÉÅÌÉú Ï¢ÖÎ£å
      }
    },
    // TMDB APIÎ°ú Ï∂îÏ≤ú ÏòÅÌôî Í∞ÄÏ†∏Ïò§Í∏∞
    async fetchRecommendations() {
      try {
        const movieId = this.$route.params.id; // URL ÌååÎùºÎØ∏ÌÑ∞ÏóêÏÑú ÏòÅÌôî ID Í∞ÄÏ†∏Ïò§Í∏∞
        const response = await tmdb.get(`/movie/${movieId}/recommendations`, {
          params: { language: "ko-KR" },
        });
        this.recommendations = response.data.results.slice(0, 5); // ÏµúÎåÄ 5Í∞ú Ï∂îÏ≤ú ÏòÅÌôîÎßå Ï†ÄÏû•
      } catch (error) {
        console.error("Failed to fetch recommendations:", error);
        alert("Ï∂îÏ≤ú ÏòÅÌôîÎ•º Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
      }
    },
    // Ï∞úÌïòÍ∏∞ Í∏∞Îä•
    addToWishlist() {
      const wishlist = JSON.parse(localStorage.getItem("wishlist")) || [];
      const alreadyInWishlist = wishlist.some((item) => item.id === this.movie.id);

      if (!alreadyInWishlist) {
        wishlist.push({
          id: this.movie.id,
          title: this.movie.title,
          release_date: this.movie.release_date,
          poster_path: this.movie.poster_path,
          vote_average: this.movie.vote_average,
        });
        localStorage.setItem("wishlist", JSON.stringify(wishlist));
        alert("Ï∞úÌïú ÏΩòÌÖêÏ∏†Ïóê Ï∂îÍ∞ÄÎêòÏóàÏäµÎãàÎã§!");
      } else {
        alert("Ïù¥ÎØ∏ Ï∞úÌïú ÏΩòÌÖêÏ∏†ÏûÖÎãàÎã§.");
      }
    },
  },
  watch: {
    // URLÏùò ÏòÅÌôî IDÍ∞Ä Î≥ÄÍ≤ΩÎê† Îïå Îç∞Ïù¥ÌÑ∞Î•º Îã§Ïãú Î°úÎìú
    "$route.params.id": {
      immediate: true,
      handler() {
        this.loading = true;
        this.fetchMovieDetails();
        this.fetchRecommendations();
      },
    },
  },
};
</script>

