<template>
  <div>
    <h1>Search Results</h1>

    <!-- ÌïÑÌÑ∞ÎßÅ UI -->
    <div class="filter-container">
      <select v-model="selectedGenre" @change="filterMovies">
        <option value="">Î™®Îì† Ïû•Î•¥</option>
        <option v-for="genre in genres" :key="genre.id" :value="genre.id">
          {{ genre.name }}
        </option>
      </select>

      <select v-model="selectedRating" @change="filterMovies">
        <option value="">Î™®Îì† ÌèâÏ†ê</option>
        <option value="8">8Ï†ê Ïù¥ÏÉÅ</option>
        <option value="7">7Ï†ê Ïù¥ÏÉÅ</option>
        <option value="6">6Ï†ê Ïù¥ÏÉÅ</option>
      </select>

      <select v-model="sortOption" @change="sortMovies">
        <option value="popularity">Ïù∏Í∏∞Ïàú</option>
        <option value="release_date">ÏµúÏã† Í∞úÎ¥âÏùº</option>
        <option value="vote_average">ÌèâÏ†êÏàú</option>
      </select>

      <button @click="resetFilters">Ï¥àÍ∏∞Ìôî</button>
    </div>

    <!-- Í≤∞Í≥º ÌëúÏãú -->
    <div class="movie-grid">
      <div
        v-for="movie in filteredMovies"
        :key="movie.id"
        class="movie-item"
      >
        <img
          :src="movie.poster_path 
            ? `https://image.tmdb.org/t/p/w500${movie.poster_path}` 
            : 'https://via.placeholder.com/500x750?text=No+Image'"
          :alt="movie.title"
        />
        <h3>{{ movie.title }}</h3>
        <p>{{ movie.release_date || 'Í∞úÎ¥âÏùº Ï†ïÎ≥¥ ÏóÜÏùå' }}</p>
      </div>
    </div>

    <!-- Î°úÎî© ÏÉÅÌÉú -->
    <div v-if="loading" class="loading">Î°úÎî© Ï§ë...</div>

    <!-- Î¨¥Ìïú Ïä§ÌÅ¨Î°§ Í∞êÏßÄ ÏöîÏÜå -->
    <div ref="observer" class="scroll-observer"></div>
  </div>
</template>

<script>
import tmdb from '@/api/tmdb';

export default {
  data() {
    return {
      movies: [], // Ï†ÑÏ≤¥ ÏòÅÌôî Î™©Î°ù
      filteredMovies: [], // ÌïÑÌÑ∞ÎßÅÎêú ÏòÅÌôî Î™©Î°ù
      genres: [], // Ïû•Î•¥ Îç∞Ïù¥ÌÑ∞
      selectedGenre: '', // ÏÑ†ÌÉùÎêú Ïû•Î•¥
      selectedRating: '', // ÏÑ†ÌÉùÎêú ÌèâÏ†ê
      sortOption: 'popularity', // Ï†ïÎ†¨ Í∏∞Ï§Ä
      page: 1, // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ
      totalPages: null, // Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ Ïàò
      loading: false, // Î°úÎî© ÏÉÅÌÉú
    };
  },
  methods: {
    async fetchGenres() {
      const response = await tmdb.get('/genre/movie/list', { params: { language: 'ko-KR' } });
      this.genres = response.data.genres;
    },
    async fetchMovies() {
      if (this.loading || (this.totalPages && this.page > this.totalPages)) return;

      this.loading = true;
      try {
        const response = await tmdb.get('/search/movie', {
          params: {
            query: this.$route.query.q || '',
            page: this.page,
            language: 'ko-KR',
          },
        });
        const { results, total_pages } = response.data;
        this.movies.push(...results);
        this.filteredMovies = [...this.movies];
        this.totalPages = total_pages;
      } catch (error) {
        console.error('Error fetching movies:', error);
      } finally {
        this.loading = false;
      }
    },
    filterMovies() {
      this.filteredMovies = this.movies.filter(movie => {
        const matchesGenre = this.selectedGenre ? movie.genre_ids.includes(parseInt(this.selectedGenre)) : true;
        const matchesRating = this.selectedRating ? movie.vote_average >= parseFloat(this.selectedRating) : true;
        return matchesGenre && matchesRating;
      });
    },
    sortMovies() {
      this.filteredMovies.sort((a, b) => {
        if (this.sortOption === 'popularity') return b.popularity - a.popularity;
        if (this.sortOption === 'release_date') return new Date(b.release_date) - new Date(a.release_date);
        if (this.sortOption === 'vote_average') return b.vote_average - a.vote_average;
      });
    },
    resetFilters() {
      this.selectedGenre = '';
      this.selectedRating = '';
      this.sortOption = 'popularity';
      this.filteredMovies = [...this.movies];
    },
    observeScroll() {
      const observer = new IntersectionObserver(entries => {
        if (entries[0].isIntersecting) {
          this.page++;
          this.fetchMovies();
        }
      });
      observer.observe(this.$refs.observer);
    },
  },
  created() {
    this.fetchGenres();
    this.fetchMovies();
  },
  mounted() {
    this.observeScroll();
  },
};
</script>

