<script>
import tmdb from "@/api/tmdb";

export default {
  name: "NowPlayingView",
  data() {
    return {
      movies: [], // ÌòÑÏû¨ ÏÉÅÏòÅ Ï§ëÏù∏ ÏòÅÌôî Îç∞Ïù¥ÌÑ∞
      topBookingMovies: [], // ÏòàÎß§Ïú® Top 3 ÏòÅÌôî Îç∞Ïù¥ÌÑ∞
      loading: true, // Î°úÎî© ÏÉÅÌÉú
      titleVisible: false, // ÌÉÄÏù¥ÌãÄ ÌëúÏãú Ïó¨Î∂Ä
    };
  },
  async created() {
    try {
      // ÌòÑÏû¨ ÏÉÅÏòÅ Ï§ë ÏòÅÌôî Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞
      const response = await tmdb.get("/movie/now_playing", {
        params: { language: "ko-KR" },
      });
      this.movies = response.data.results;

      // ÏòàÎß§Ïú® Top 3 ÏòÅÌôî Îç∞Ïù¥ÌÑ∞ Ï∂îÏ∂ú (ÏûÑÏãúÎ°ú ÌèâÏ†ê Í∏∞Ï§Ä Ï†ïÎ†¨)
      this.topBookingMovies = [...this.movies]
        .sort((a, b) => b.vote_average - a.vote_average) // ÎÜíÏùÄ ÌèâÏ†ê ÏàúÏúºÎ°ú Ï†ïÎ†¨
        .slice(0, 3); // ÏÉÅÏúÑ 3Í∞ú ÏòÅÌôî Ï∂îÏ∂ú
    } catch (error) {
      console.error("Failed to fetch now playing movies:", error);
      alert("ÌòÑÏû¨ ÏÉÅÏòÅ Ï§ëÏù∏ ÏòÅÌôîÎ•º Î∂àÎü¨Ïò§Îäî Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    } finally {
      this.loading = false; // Î°úÎî© ÏÉÅÌÉú Ï¢ÖÎ£å
    }
  },
  mounted() {
    // ÌÉÄÏù¥ÌãÄ Ïï†ÎãàÎ©îÏù¥ÏÖò ÌôúÏÑ±Ìôî
    setTimeout(() => {
      this.titleVisible = true;
    }, 500); // 0.5Ï¥à ÌõÑ ÌÉÄÏù¥ÌãÄ ÌëúÏãú
  },
};
</script>

