<template>
  <div class="wishlist-container">
    <!-- ÌÉÄÏù¥ÌãÄ -->
    <h1 :class="{ 'main-title': true, show: titleVisible }">
      üé• ÎÇ¥Í∞Ä Ï∞úÌïú ÏΩòÌÖêÏ∏†
    </h1>
    <!-- Ï∞úÌïú ÏΩòÌÖêÏ∏†Í∞Ä ÏóÜÏùÑ Í≤ΩÏö∞ -->
    <div v-if="wishlist.length === 0" class="empty-message">
      <p>Ï∞úÌïú ÏΩòÌÖêÏ∏†Í∞Ä ÏóÜÏäµÎãàÎã§.</p>
    </div>

    <!-- Ï∞úÌïú ÏΩòÌÖêÏ∏†Í∞Ä ÏûàÏùÑ Í≤ΩÏö∞ -->
    <div v-else class="wishlist-grid">
      <div v-for="item in wishlist" :key="item.id" class="wishlist-item">
        <img 
          :src="item.poster_path 
            ? `https://image.tmdb.org/t/p/w500${item.poster_path}` 
            : 'https://via.placeholder.com/500x750?text=No+Image'"
          :alt="item.title"
          class="wishlist-poster"
        />
        <div class="wishlist-info">
          <h2 class="wishlist-title">{{ item.title }}</h2>
          <p><strong>Í∞úÎ¥âÏùº:</strong> {{ item.release_date || 'Ï†ïÎ≥¥ ÏóÜÏùå' }}</p>
          <p><strong>ÌèâÏ†ê:</strong> {{ item.vote_average }}/10</p>
          <button @click="removeFromWishlist(item.id)" class="remove-btn">ÏÇ≠Ï†ú</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "WishlistView",
  data() {
    return {
      wishlist: [], // Ï∞úÌïú ÏòÅÌôî Îç∞Ïù¥ÌÑ∞
      titleVisible: false, // ÌÉÄÏù¥ÌãÄ ÌëúÏãú Ïó¨Î∂Ä
    };
  },
  methods: {
    // Ï∞ú Î¶¨Ïä§Ìä∏ Î∂àÎü¨Ïò§Í∏∞
    loadWishlist() {
      const storedWishlist = JSON.parse(localStorage.getItem("wishlist")) || [];
      this.wishlist = storedWishlist;
    },
    // Ï∞ú Î¶¨Ïä§Ìä∏ÏóêÏÑú Ìï≠Î™© ÏÇ≠Ï†ú
    removeFromWishlist(id) {
      this.wishlist = this.wishlist.filter((item) => item.id !== id);
      localStorage.setItem("wishlist", JSON.stringify(this.wishlist));
      alert("Ï∞úÌïú ÏΩòÌÖêÏ∏†Í∞Ä ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.");
    },
  },
  mounted() {
    this.loadWishlist();
    setTimeout(() => {
      this.titleVisible = true; // ÌÉÄÏù¥ÌãÄ ÌëúÏãú
    }, 500); // 0.5Ï¥à ÌõÑ ÌÉÄÏù¥ÌãÄ ÌëúÏãú
  },
};
</script>

