import { createRouter, createWebHistory } from 'vue-router';
import PopularMovies from '../views/PopularMovies.vue';
import MovieDetailView from '../views/MovieDetailView.vue';
import AboutView from '../views/AboutView.vue';
import MovieList from '../components/MovieList.vue';
import MovieSearch from '../views/MovieSearch.vue';
import GenreFilterView from '../views/GenreFilterView.vue';
import SignInView from '../components/SignIn.vue';
import WishlistView from '../views/WishlistView.vue';
import NowPlayingView from '../views/NowPlayingView.vue';
import HomePage from '../views/HomePage.vue';

// ÎùºÏö∞Ìä∏ Ï†ïÏùò
const routes = [
  {
    path: '/signin',
    name: 'SignIn',
    component: SignInView,
    meta: { hideHeader: true }, // Ìó§Îçî Ïà®ÍπÄ ÏÑ§Ï†ï
  },
  {
    path: '/',
    name: 'Home',
    component: HomePage,
    meta: { requiresAuth: true },
  },
  {
    path: '/popular',
    name: 'PopularMovies',
    component: PopularMovies,
    meta: { requiresAuth: true },
  },
  {
    path: '/movie/:id',
    name: 'MovieDetail',
    component: MovieDetailView,
    meta: { requiresAuth: true },
  },
  {
    path: '/now_playing',
    name: 'nowplaying',
    component: NowPlayingView,
    meta: { requiresAuth: true },
  },
  {
    path: '/about',
    name: 'About',
    component: AboutView,
    meta: { requiresAuth: true },
  },
  {
    path: '/movielist',
    name: 'MovieList',
    component: MovieList,
    meta: { requiresAuth: true },
  },
  {
    path: '/search',
    name: 'SearchMovies',
    component: MovieSearch,
    meta: { requiresAuth: true },
  },
  {
    path: '/genres',
    name: 'GenreFilter',
    component: GenreFilterView,
    meta: { requiresAuth: true },
  },
  {
    path: '/wishlist',
    name: 'Wishlist',
    component: WishlistView,
    meta: { requiresAuth: true },
  },
];

// ÎùºÏö∞ÌÑ∞ ÏÉùÏÑ±
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

// Ï†ÑÏó≠ Í∞ÄÎìú: Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌïú Í≤ΩÎ°ú Î≥¥Ìò∏
router.beforeEach((to, from, next) => {
  const isLoggedIn = localStorage.getItem('isLoggedIn') === 'true'; // Î°úÍ∑∏Ïù∏ Ïó¨Î∂Ä ÌôïÏù∏
  const splashScreen = document.querySelector("#splash-screen"); // Ïä§ÌîåÎûòÏãú ÌôîÎ©¥ ÏöîÏÜå

  // Ïä§ÌîåÎûòÏãú ÌôîÎ©¥Ïù¥ ÏûàÏúºÎ©¥ Ï†úÍ±∞
  if (splashScreen) {
    splashScreen.style.opacity = "0";
    splashScreen.style.visibility = "hidden";
  }

  // Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌïú Í≤ΩÎ°ú Ï†ëÍ∑º Ïãú
  if (to.meta.requiresAuth && !isLoggedIn) {
    alert('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
    next({ name: 'SignIn' });
  } else if (to.name === 'SignIn' && isLoggedIn) {
    // Ïù¥ÎØ∏ Î°úÍ∑∏Ïù∏Îêú ÏÉÅÌÉúÏóêÏÑú Î°úÍ∑∏Ïù∏/ÌöåÏõêÍ∞ÄÏûÖ ÌéòÏù¥ÏßÄ Ï†ëÍ∑º Ïãú Î©îÏù∏ÏúºÎ°ú Î¶¨Îã§Ïù¥Î†âÌä∏
    next({ name: 'Home' });
  } else {
    // Î™®Îì† Ï°∞Í±¥ÏùÑ Ï∂©Ï°±Ìïú Í≤ΩÏö∞ Ïù¥Îèô
    next();
  }
});

export default router;
