      this.loggedInUser = "";
      alert("Î°úÍ∑∏ÏïÑÏõÉÎêòÏóàÏäµÎãàÎã§.");
      this.$router.push("/signin");
    },
    checkLoginStatus() {
      this.isLoggedIn = localStorage.getItem("isLoggedIn") === "true";
      this.loggedInUser = localStorage.getItem("loggedInUser") || ""; // ÎãâÎÑ§ÏûÑ Í∞ÄÏ†∏Ïò§Í∏∞
      console.log("Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú:", this.isLoggedIn, "ÎãâÎÑ§ÏûÑ:", this.loggedInUser);
    },
  },
  created() {
    this.checkLoginStatus();
  },
  watch: {
    $route() {
      this.checkLoginStatus();
    },
  },
};
</script>


