<script>
import tmdb from "@/api/tmdb";

export default {
  name: "GenreFilterView",
  data() {
    return {
      genres: [], // Ïû•Î•¥ Î™©Î°ù
      movies: [], // ÌïÑÌÑ∞ÎßÅÎêú ÏòÅÌôî Îç∞Ïù¥ÌÑ∞
      selectedGenre: "", // ÏÑ†ÌÉùÎêú Ïû•Î•¥ ID
      loading: true, // Î°úÎî© ÏÉÅÌÉú
      titleVisible: false, // ÌÉÄÏù¥ÌãÄ Ïï†ÎãàÎ©îÏù¥ÏÖò ÏÉÅÌÉú
    };
  },
  async created() {
    await this.fetchGenres(); // Ïû•Î•¥ Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
    await this.fetchMovies(); // Î™®Îì† ÏòÅÌôî Í∞ÄÏ†∏Ïò§Í∏∞
  },
  mounted() {
    setTimeout(() => {
      this.titleVisible = true; // ÌÉÄÏù¥ÌãÄ Ïï†ÎãàÎ©îÏù¥ÏÖò ÌôúÏÑ±Ìôî
    }, 500); // ÏïΩÍ∞ÑÏùò ÏßÄÏó∞ ÌõÑ Ïï†ÎãàÎ©îÏù¥ÏÖò Ïã§Ìñâ
  },
  methods: {
    // Ïû•Î•¥ Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
    async fetchGenres() {
      try {
        const response = await tmdb.get("/genre/movie/list", {
          params: { language: "ko-KR" },
        });
        this.genres = response.data.genres;
      } catch (error) {
        console.error("Failed to fetch genres:", error);
        alert("Ïû•Î•¥ Î™©Î°ùÏùÑ Î∂àÎü¨Ïò§Îäî Îç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      }
    },
    // ÏòÅÌôî Í∞ÄÏ†∏Ïò§Í∏∞ (ÌïÑÌÑ∞ÎßÅ Ìè¨Ìï®)
    async fetchMovies() {
      this.loading = true;
      try {
        const response = await tmdb.get("/discover/movie", {
          params: {
            language: "ko-KR",
            with_genres: this.selectedGenre || undefined, // Ïû•Î•¥ ÌïÑÌÑ∞ Ï†ÅÏö©
          },
        });
        this.movies = response.data.results;
      } catch (error) {
        console.error("Failed to fetch movies:", error);
        alert("ÏòÅÌôî Î™©Î°ùÏùÑ Î∂àÎü¨Ïò§Îäî Îç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      } finally {
        this.loading = false;
      }
    },
    // Ïû•Î•¥ Î≥ÄÍ≤Ω Ïãú ÏòÅÌôî ÌïÑÌÑ∞ÎßÅ
    async filterMovies() {
      await this.fetchMovies();
    },
  },
};
</script>

