<template>
  <div class="auth-container">
    <!-- Î°úÍ∑∏Ïù∏ Î∞è ÌöåÏõêÍ∞ÄÏûÖ Ïπ¥Îìú -->
    <div class="auth-card">
      <!-- Î°úÍ≥† -->
      <div class="auth-logo">
        <h1>YeonPlay</h1>
      </div>
      <transition name="slide" mode="out-in">
        <!-- Î°úÍ∑∏Ïù∏ Ìèº -->
        <form v-if="isLoginVisible" @submit.prevent="handleLogin" key="login">
          <h2 class="form-title">Î°úÍ∑∏Ïù∏</h2>
          <div class="input-group">
            <input type="email" v-model="loginForm.email" placeholder="Ïù¥Î©îÏùº" required />
          </div>
          <div class="input-group">
            <input type="password" v-model="loginForm.password" placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏" required />
          </div>
          <div class="checkbox-group">
            <label>
              <input type="checkbox" v-model="loginForm.rememberMe" />
              <span>Remember me</span>
            </label>
          </div>
          <button type="submit" :disabled="!isLoginFormValid" class="form-btn">Î°úÍ∑∏Ïù∏</button>
          <p class="switch-form">
            Í≥ÑÏ†ïÏù¥ ÏóÜÏúºÏã†Í∞ÄÏöî? <span @click="toggleCard">ÌöåÏõêÍ∞ÄÏûÖ</span>
          </p>
        </form>

        <!-- ÌöåÏõêÍ∞ÄÏûÖ Ìèº -->
        <form v-else @submit.prevent="handleRegister" key="register">
          <h2 class="form-title">ÌöåÏõêÍ∞ÄÏûÖ</h2>
          <div class="input-group">
            <input type="text" v-model="registerForm.nickname" placeholder="ÎãâÎÑ§ÏûÑ" required />
          </div>
          <div class="input-group">
            <input type="email" v-model="registerForm.email" placeholder="Ïù¥Î©îÏùº" required />
          </div>
          <div class="input-group">
            <input type="password" v-model="registerForm.password" placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏" required />
          </div>
          <div class="input-group">
            <input
              type="password"
              v-model="registerForm.confirmPassword"
              placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏"
              required
            />
          </div>
          <div class="checkbox-group">
            <label>
              <input type="checkbox" v-model="registerForm.acceptTerms" />
              <span>ÏïΩÍ¥ÄÏóê ÎèôÏùòÌï©ÎãàÎã§</span>
            </label>
          </div>
          <button type="submit" :disabled="!isRegisterFormValid" class="form-btn">ÌöåÏõêÍ∞ÄÏûÖ</button>
          <p class="switch-form">
            Ïù¥ÎØ∏ Í≥ÑÏ†ïÏù¥ ÏûàÏúºÏã†Í∞ÄÏöî? <span @click="toggleCard">Î°úÍ∑∏Ïù∏</span>
          </p>
        </form>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: "SignIn",
  data() {
    return {
      isLoginVisible: true,
      loginForm: {
        email: "",
        password: "",
        rememberMe: false,
      },
      registerForm: {
        nickname: "", // ÎãâÎÑ§ÏûÑ Ï∂îÍ∞Ä
        email: "",
        password: "",
        confirmPassword: "",
        acceptTerms: false,
      },
    };
  },
  computed: {
    isLoginFormValid() {
      return !!this.loginForm.email && !!this.loginForm.password;
    },
    isRegisterFormValid() {
      return (
        !!this.registerForm.nickname && // ÎãâÎÑ§ÏûÑ ÏûÖÎ†• Ïó¨Î∂Ä ÌôïÏù∏
        !!this.registerForm.email &&
        !!this.registerForm.password &&
        this.registerForm.password === this.registerForm.confirmPassword &&
        this.registerForm.acceptTerms
      );
    },
  },
  methods: {
    toggleCard() {
      this.isLoginVisible = !this.isLoginVisible;
    },
    handleLogin() {
      const { email, password } = this.loginForm;

      // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•Îêú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
      const storedEmail = localStorage.getItem("userEmail");
      const storedPassword = localStorage.getItem("userPassword");
      const storedNickname = localStorage.getItem("userNickname"); // ÎãâÎÑ§ÏûÑ Í∞ÄÏ†∏Ïò§Í∏∞

      if (email === storedEmail && password === storedPassword) {
        // Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ
        localStorage.setItem("isLoggedIn", "true"); // Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú Ï†ÄÏû•
        localStorage.setItem("loggedInUser", storedNickname); // ÎãâÎÑ§ÏûÑÏùÑ Î°úÍ∑∏Ïù∏ ÏÇ¨Ïö©ÏûêÎ°ú ÏÑ§Ï†ï

        alert("Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ!");

        // Ìôà ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
        this.$router.push({ name: "Home" }); // "Home"ÏùÄ ÎùºÏö∞ÌÑ∞ÏóêÏÑú Ï†ïÏùòÎêú Í≤ΩÎ°ú Ïù¥Î¶Ñ
      } else {
        alert("Ïù¥Î©îÏùº ÎòêÎäî ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.");
      }
    },
    handleRegister() {
      const { nickname, email, password } = this.registerForm;

      // ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Î•º Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•
      localStorage.setItem("userNickname", nickname); // ÎãâÎÑ§ÏûÑ Ï†ÄÏû•
      localStorage.setItem("userEmail", email);
      localStorage.setItem("userPassword", password);
      localStorage.setItem("isLoggedIn", "false"); // Í∏∞Î≥∏Í∞íÏùÄ Î°úÍ∑∏Ïù∏ÎêòÏßÄ ÏïäÏùå

      alert("ÌöåÏõêÍ∞ÄÏûÖ ÏÑ±Í≥µ!");
      this.toggleCard(); // ÌöåÏõêÍ∞ÄÏûÖ ÏôÑÎ£å ÌõÑ Î°úÍ∑∏Ïù∏ ÌôîÎ©¥ÏúºÎ°ú Ï†ÑÌôò
    },
  },
};
</script>


